.MainDiv {
  background-color: rgb(218, 216, 216);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.mainNav {
  display: flex;
  height: 250px;
  justify-content: center;
  align-items: center;
  width: 80%;
}
.mainHeadingDiv {
  display: flex;
  height: 200px;
  flex-direction: column;
  align-items: center;
}
.mainHeadingH1 {
  font-size: 4rem;
  text-align: center;
  font-weight: 200;
}
.mainHeadingH2 {
  text-align: center;
  font-size: 1rem;
  margin-top: 1rem;
  text-align: center;
  font-weight: 300;
}
.mainHeadingSearchDiv {
  margin-top: 1rem;
  display: flex;
  position: relative;
}
.searchInputJob {
  color: #0f1115;
  font-weight: 700;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 0;

  font-size: 1.5em;
  padding: 0.5em;
  outline: none;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  text-align: center;
  background: 0 0;
  color: #fff;
  border-bottom: 2px solid #fff;
  text-align: left;
  color: #0f1115;
  background: #fff;
  border-radius: 6px;
  box-shadow: 1px 2px 4px rgb(0 0 0 / 25%);
  padding-left: 2.5em;
  background-repeat: no-repeat;
  background-size: 1em;
  background-position: 0.75em center;
  width: 100%;
  max-width: 80vw;
  border-radius: 100px;
  border: none;
  box-shadow: 0 0 0 1px var(--input-border-color), 0 2px 4px 0 rgb(0 0 0/7%),
    0 1px 1.5px 0 rgb(0 0 0/5%) !important;
}
.searchButton {
  background-color: transparent;
  position: absolute;
  border: none;
  height: 80%;
  width: 40px;
  top: 50%;
  cursor: pointer;
  left: 8%;
  transform: translate(-50%, -50%);
}
.searchPicture {
  transition: 0.2s all;
}
.searchPicture:hover {
  transform: translateY(-2px);
}
.searchPicture:active {
  transform: translateY(2px);
}
